.App{
  text-align: center;
  color: white;
  font-size: 30px;
}
.TodoList{
  width: 400px;
  border: 2px solid white;
  background: #eee;
  color: #6f134d;
  cursor: pointer;
  margin: 5px 5px;
  border-radius: 3px;
}
.Main{
  margin-top: 50px;
  margin-bottom: 50px;
}

.Done{
  text-decoration: line-through;
}
.Important{
  font-weight: bold;
}

.Flex{
  display: flex;
  justify-content: center;
}

.Button{
  height: 30px;
  margin-top: 9px;
}

h1{
  margin-top: 30px;
  margin-bottom: 20px;
  color: white;
}

.Input{
  width: 170px;
  height: 24px;
}
.Btn-info{
  background: #4b8708;
  color: white;
}

.Btn-outline-secondary{
  background: #0b2997;
  color: white;
}

body{
  background: #1a1640;
}